import "../../App.css";
import DownloadIcon from "@mui/icons-material/Download";

export default function Sobre() {
  return (
    <div
      id="sobre"
      className="min-h-screen bg-white flex flex-col text-center gap-5 text-black text-lg  font-normal"
    >
      <div className="head text-5xl mt-12 font-bold" data-aos={"slide-down"}>
        O Fit Taboão
      </div>
      <div className="flex flex-wrap flex-row gap-6 ml-8 max-w-screen-xl w-full ml-auto mr-auto">
        <div className="flex-col mt-14 flex flex-auto w-64 gap-6">
          <h3 className="text-3xl font-medium" data-aos={"fade-left"}>
            O <span className="text-yellow-600">Fit Taboão</span> tem 14 anos de construção.
          </h3>
          <p
            className="pt-5 leading-7 text-slate-900 text-justify"
            data-aos={"fade-left"}
          >
            O "condomínio" ocorre quando existe um domínio de mais de uma pessoa simultaneamente de
             um determinado bem ou partes de um bem, segundo a legislação brasileira, temos
              expressa que a ideia do direito exercido por mais de uma pessoa sobre o mesmo objeto.
          </p>
          <p
            className="text-justify leading-7 text-slate-900"
            data-aos={"fade-right"}
          >
            Este condomínio existe a mais de 14 anos, localizado estratégicamente em um excelente ponto
             geografico, acessivel para varios lugares da região metropolitana,  com boas areas de lazer,
              segurança, conforto e sossego. O objetivo deste portal é ajudar e conectar os condôminos,
               venda de produtos, serviços ou compartilhar informações sobre o condomínio.
          </p>
          <p
            className="text-left leading-7 text-slate-900"
            data-aos={"fade-right"}
          >
            Manual de boas praticas e convivência!<br/>
                <button className="py-1 mt-2 text-white mx-auto px-2 bg-blue-600 border-2 w-fit border-blue-500 rounded-3xl  hover:-translate-y-1.5 duration-[350ms] hover:duration-[350ms] hover:bg-blue-800 hover:scale-[1.023] focus:bg-blue-800 animate-zoomy">
                <a
                  href={require("../../assets/files/manual_convivencia.pdf")}
                  download={"manual_convivencia.pdf"}
                >
                  clique aqui para dicas
                  <DownloadIcon />
                </a>
              </button>
            </p>
            <br/>
        </div>
        <div
          class="relative flex-auto w-32   sm:rounded-lg  pl-4"
          data-aos={"slide-left"}
        >
          <div className="motion-safe:animate-zoomy">
            <img
              src={require("../../assets/images/foto_fit_2.jpg")}
              alt="Condominio Fit Taboão"
              className="rounded-full border-solid cursor-pointer  border-[8px] border-stone-600 min-h-fit mx-auto  max-w-[370px]"
            />
          </div>
          <button className="py-1 mt-5 text-white mx-auto px-2 bg-blue-600 border-2 w-fit border-blue-500 rounded-3xl  hover:-translate-y-1.5 duration-[350ms] hover:duration-[350ms] hover:bg-blue-800 hover:scale-[1.023] focus:bg-blue-800 animate-zoomy">
            <a
              href={require("../../assets/files/planta_tenda.pdf")}
              download={"planta_tenda.pdf"}
            >
              Planta do Predio
              <DownloadIcon />
            </a>
          </button><br/>
          <button className="py-1 mt-2 text-white mx-auto px-2 bg-blue-600 border-2 w-fit border-blue-500 rounded-3xl  hover:-translate-y-1.5 duration-[350ms] hover:duration-[350ms] hover:bg-blue-800 hover:scale-[1.023] focus:bg-blue-800 animate-zoomy">
            <a
              href={require("../../assets/files/planta_tenda.pdf")}
              download={"planta_tenda.pdf"}
            >
              Regulamento Interno
              <DownloadIcon />
            </a>
          </button>
        </div>
      </div>
      <br/>
    </div>
  );
}
